import { getAlbumid,getMp3Url } from '@/network/detail.js'
export default {
	onLoad(option) {
		this.imgUrl = getAlbumid(option.albumid)
		this.mp3Player = uni.createInnerAudioContext()
		this.mp3Player.autoplay = true
		this.dealUrl(option.songmid)
	},
	data() {
		return {
			imgUrl:'',
			currentMode:0,
			currentStatus:1,
			mp3Player:'',
			mp3Options:{
				url:""
			},
			modeList:[
				{
					name:'列表循环',
					image:'../../static/detail/cm2_icn_loop@2x.png'
				},
				{
					name:'单曲循环',
					image:'../../static/detail/cm2_icn_one@2x.png'
				},
				{
					name:'随机播放',
					image:'../../static/detail/cm2_icn_shuffle@2x.png'
				},
			],
			playList:[
				{
					name:'暂停',
					image:'../../static/detail/cm2_fm_btn_play@2x.png'
				},
				{
					name:'播放',
					image:'../../static/detail/cm2_fm_btn_pause@2x.png'
				},
			]
		}
	},
	
	methods: {
		modeClick(index){
			if(index < this.modeList.length - 1){
				this.currentMode ++
			}else{
				this.currentMode = 0
			}
		},
		statusCLick(index){
			if(index < this.playList.length - 1){
				this.currentStatus ++
			}else{
				this.currentStatus = 0
			}
		},
		async dealUrl(songmid){
			this.mp3Options.url = await getMp3Url(songmid)
				this.mp3Player.src = this.mp3Options.url
				this.mp3Player.play()
		},
		
	},
	onUnload() {
		this.mp3Player.destroy()
	}
}
